import React, { Component } from 'react';
import '../src/index.css';
import ResultComponent from './compoment/ResultComponent';
import KeypadComponent from "./compoment/KeypadComponent";


class MotherComponent extends Component {
  state = { 
      result: ''
   }

   onClick = button => {

    if(button === "="){
        this.calculate()
    }

    else if(button === "C"){
        this.reset()
    }
    else if(button === "CE"){
        this.backspace()
    }

    else {
        this.setState({
            result: this.state.result + button
        })
    }
};

   calculate = () => {
    try {
        this.setState({
            // eslint-disable-next-line
            result: (eval(this.state.result) || "" ) + ""
        })
    } catch (e) {
        this.setState({
            result: "error"
        })

    }
};

reset = () => {
    this.setState({
        result: ""
    })
};

backspace = () => {
    this.setState({
        result: this.state.result.slice(0, -1)
    })
};

  render() { 
    return ( <div className='container'>
                <div className='row'>
                    <div className='col-md-5 calculator-body mx-auto my-2  text-light text-center' style={{backgroundColor: 'purple'}}>
                        <h1> DER RECHNER MIT REACT-JS</h1>
                        <hr/>
                        <ResultComponent result={this.state.result} />
                        <KeypadComponent onClick={this.onClick} />
                    </div>
                </div>
            </div>
     );
  }
}
 
export default MotherComponent;

