import React, {Component} from 'react';

class KeyPadComponent extends Component {

    render() {
        return (
            <div className="button col-md-5">
              <button className=' btn-primary mt-2' name="(" onClick={e => this.props.onClick(e.target.name)}>(</button>
              <button className=' btn-danger mt-2' name="CE" onClick={e => this.props.onClick(e.target.name)}>CE</button>
              <button className=' btn-primary mt-2' name=")" onClick={e => this.props.onClick(e.target.name)}>)</button>
              <button  className=' btn-primary mt-2'name="C" onClick={e => this.props.onClick(e.target.name)}>C</button><br/>

                  <hr />
              <button name="1" onClick={e => this.props.onClick(e.target.name)}>1</button>
              <button name="2" onClick={e => this.props.onClick(e.target.name)}>2</button>
              <button name="3" onClick={e => this.props.onClick(e.target.name)}>3</button>
              <button className='btn-primary' name="+" onClick={e => this.props.onClick(e.target.name)}>+</button><br/>


              <button name="4" onClick={e => this.props.onClick(e.target.name)}>4</button>
              <button name="5" onClick={e => this.props.onClick(e.target.name)}>5</button>
              <button name="6" onClick={e => this.props.onClick(e.target.name)}>6</button>
              <button className='btn-primary' name="-" onClick={e => this.props.onClick(e.target.name)}>-</button><br/>

              <button name="7" onClick={e => this.props.onClick(e.target.name)}>7</button>
              <button name="8" onClick={e => this.props.onClick(e.target.name)}>8</button>
              <button name="9" onClick={e => this.props.onClick(e.target.name)}>9</button>
              <button className='btn-primary' name="*" onClick={e => this.props.onClick(e.target.name)}>x</button><br/>


              <button className='btn-primary' name="." onClick={e => this.props.onClick(e.target.name)}>.</button>
              <button name="0" onClick={e => this.props.onClick(e.target.name)}>0</button>
              <button className='' style={{backgroundColor: 'goldenrod'}} name="=" onClick={e => this.props.onClick(e.target.name)}>=</button>
              <button className='btn-primary' name="/" onClick={e => this.props.onClick(e.target.name)}>รท</button><br/>
          </div>
        );
    }
}


export default KeyPadComponent;
