import React, {Component} from "react";

class ResultComponent extends Component {


    render() { 
        let {result} = this.props
        return (    <div className='row'>
                        <div className='resultComponent col-md-6'>
                            <p>{result}</p>
                        </div>
                    </div>

         );
    }
}
 
export default ResultComponent;
